.benefits {
  margin: 0 auto;
  max-width: 1279px;
  padding: 166px 0 48px;
}

.benefits__title {
  margin-top: 0;
  margin-bottom: 72px;
  font-size: 32px;
  line-height: 40px;
  font-weight: 800;
  color: rgba(255, 255, 255, 1);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
}

.benefits__title-colortext {
  color: rgba(255, 19, 109, 1);
}

.benefits__table {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  display: flex;
}

.benefits__table-cell {
  max-width: 394px;
  margin-right: 48px;
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
}

.benefits__table-cell:last-of-type {
  margin-right: 0;
}

.benefits__table-cell-title {
  margin: 16px 0;
  font-weight: 800;
  font-size: 18px;
  line-height: 22.5px;
  text-transform: uppercase;
  letter-spacing: -0.015em;
  color: rgba(255, 255, 255, 1);
  text-align: center;
}

.benefits__table-cell-text {
  margin: 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.5);
  letter-spacing: -0.015em;
  text-align: center;
}

@media screen and (max-width: 1400px) {
  .benefits {
    max-width: 1024px;
  }
}

@media screen and (max-width: 1200px) {
  .benefits {
    padding: 166px 60px 48px;
  }
}

@media screen and (max-width: 768px) {
  .benefits {
    padding: 90px 60px 24px;
  }

  .benefits__title {
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 48px;
    text-align: center;
  }

  .benefits__table {
    flex-direction: column;
    align-items: center;
  }

  .benefits__table-cell {
    margin-right: 0;
    margin-bottom: 24px;
  }

  .benefits__table-cell:last-of-type {
    margin-bottom: 0;
  }

  .benefits__table-cell-title {
    font-size: 16px;
    line-height: 20px;
  }

  .benefits__table-cell-text {
    font-size: 14px;
    line-height: 18px;
  }
}

@media screen and (max-width: 576px) {
  .benefits {
    padding: 90px 14px 24px;
  }
}