@import "../../../../styles/colors.scss";
@import "../../../../styles/breakpoints";

.wrapper {
  max-width: 100%;
  padding: 32px;
  background: rgba(16, 15, 15, 0.5);
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 32px;
  margin-right: 5px;
  margin-left: 5px;

  @include tablet {
    max-width: 305px;
    padding: 24px;
  }
}

.image {
  border-radius: 16px;
  margin-bottom: 32px;
  height: 245px;
  width: 459px;

  @include tablet {
    max-width: 305px;
  }
}

.videoWrapper {
  position: relative;
  // padding-top: 56.25%; /* 720 / 1280 = 0.5625 */
  border-radius: 5px;
}

.date {
  font-weight: 300;
  font-size: 12px;
  letter-spacing: -0.015em;
  color: $white-50;

  @include tablet {
    font-size: 10px;
  }
}

.heading {
  margin-top: 32px;
  font-weight: 800;
  font-size: 18px;
  letter-spacing: -0.015em;
  text-transform: uppercase;
  color: $white;

  @include tablet {
    font-size: 16px;
  }
}

.text {
  font-weight: 300;
  font-size: 16px;
  letter-spacing: -0.015em;
  color: $white-50;

  @include tablet {
    font-size: 14px;
  }
}